import { KeyboardNavigationStrategy } from "../../../common/keyboard-navigation/keyboard-navigation-strategy";
import { TabsCssClasses } from "../tabs-css-classes";
export class TabItemKbdStrategy extends KeyboardNavigationStrategy {
    constructor(tabs, navigator, targetElement) {
        super(navigator, targetElement);
        this.tabs = tabs;
    }
    queryItems() {
        return this.queryItemsBySelector(`:scope.${TabsCssClasses.Item}`);
    }
}
